<template>
	<div id="seckill-post">
		<public-header title="添加抢购商品"></public-header>
		<div class="content">
			<van-cell-group>
				<van-cell class="no-fix flex ">
					<div class="padding-10-b flex-lr">
						<div>商品图片</div>
						<div class="c-gray">此图片为商品列表页图片</div>
					</div>
					<div class="goods-thumb-wrap">
						<img class="goods-thumb " :src="records.thumb" alt="" v-if="records.thumb.length > 0">
						<img class="goods-thumb " src="static/img/add_pic.png" alt="" v-else>
						<input class="weui-uploader__input" type="file" multiple="multiple" accept="image/*" @change="onUploadImage($event)">
					</div>
				</van-cell>
				<van-cell>
					<div class="upload-img">
						<div class="flex-lr goods-img">
							<div>商品图集</div>
							<div class="c-gray">最多显示4张，此图片为商品图集</div>
						</div>
						<uploader @onGetUrl="onChangeThumbs" @delete="onChangeThumbs" :max="4" :value="thumbs"></uploader>
					</div>
				</van-cell>
				<van-field
					v-model="records.name"
					label="商品名称"
					placeholder="请填写商品名称"
					input-align="right"
				/>
				<van-field
					v-model="records.oldprice"
					label="商品原价"
					placeholder="请填写商品原价"
					input-align="right"
				/>
				<van-field
					v-model="records.price"
					label="商品现价"
					placeholder="请填写商品现价"
					input-align="right"
				/>
				<van-field
					v-model="records.total"
					label="库存"
					placeholder="请填写商品库存"
					input-align="right"
				/>
			</van-cell-group>
			<van-cell-group class="margin-10-t">
				<van-cell title="商品分类">
					<div slot="right-icon" class="flex" @click="onTogglePopup">
						<span>{{records.category_title ? records.category_title : '请选择'}}</span>
						<van-icon name="arrow"></van-icon>
					</div>
				</van-cell>
				<van-field
					v-model="records.click"
					label="关注度"
					placeholder="请填写关注度"
					input-align="right"
				/>
				<van-cell title="活动开始时间">
					<div slot="right-icon" class="flex" @click="onToggleDatePicker('start')">
						<span>{{this.records.starttime_cn ? this.records.starttime_cn: '点击选择'}}</span>
						<van-icon name="arrow"></van-icon>
					</div>
				</van-cell>
				<van-cell title="活动结束时间">
					<div slot="right-icon" class="flex" @click="onToggleDatePicker('end')">
						<span>{{this.records.endtime_cn ? this.records.endtime_cn: '点击选择'}}</span>
						<van-icon name="arrow"></van-icon>
					</div>
				</van-cell>
				<van-field
					v-model="records.use_limit_day"
					label="消费截止时间"
					placeholder="用户购买成功后几天后失效"
					input-align="right"
				/>
				<van-cell>
					<div slot="title">活动状态</div>
					<iswitch :value="records.status" condition-open="1" condition-close="0" slot="right-icon" text-open="ON" text-close="OFF" @change="jsToggleSwitch" keys="records.status"></iswitch>
				</van-cell>
				<van-field
					v-model="records.content"
					label="简单描述"
					placeholder="请简单描述商品，控制在30字以内"
					input-align="right"
				/>
				<van-field
					v-model="records.description"
					label="商品详情"
					type="textarea"
					placeholder="请填写商品详情"
					rows="1"
					input-align="right"
					autosize
				/>
				<van-field
					v-model="records.buy_note"
					label="购买须知"
					type="textarea"
					placeholder="请填写购买须知"
					rows="1"
					input-align="right"
					autosize
				/>
			</van-cell-group>
			<van-cell-group class="margin-10-t">
				<van-cell>
					<div slot="title">高级设置</div>
					<iswitch :value="bestsetting" condition-open="1" condition-close="0" slot="right-icon" text-open="ON" text-close="OFF" @change="jsToggleSwitch" keys="bestsetting"></iswitch>
				</van-cell>
			</van-cell-group>
			<template v-if="bestsetting == '1'">
				<van-cell-group class="margin-10-t">
					<van-field
						v-model="records.falsejoinnum"
						label="虚拟参与人数"
						placeholder="请输入虚拟参与人数"
						input-align="right"
					/>
					<van-field
						v-model="records.falselooknum"
						label="虚拟浏览量"
						placeholder="请输入虚拟浏览量"
						input-align="right"
					/>
					<van-field
						v-model="records.falsesharenum"
						label="虚拟分享次数"
						placeholder="请输入虚拟分享次数"
						input-align="right"
					/>
				</van-cell-group>
			</template>
			<div class="padding-15">
				<van-button size="normal" block class="bg-info" @click="onSubmit">提交</van-button>
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
		<!--商品分类picker-->
		<van-popup v-model="goodsCategory" position="bottom">
			<van-picker
					show-toolbar
					title="所属分类"
					:columns="category"
					value-key="title"
					@cancel="onTogglePopup"
					@confirm="onConfirmCategory"
			/>
		</van-popup>
		<!--时间picker-->
		<van-popup class="popup-time" v-model="datePicker.status" position="bottom">
			<van-datetime-picker
					v-model="datePicker.currentDate"
					type="datetime"
					:min-data="datePicker.currentDate"
					:max-date="datePicker.maxDate"
					@confirm="onConfirmTime"
					@cancel="onToggleDatePicker"
			/>
		</van-popup>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import iswitch from "@/components/switch"
	import {getData, setData} from "@/controller/funcCommon"
	import Uploader from '@/components/uploader'
	export default {
		data() {
			return {
				id: 0,
				thumb: {},
				thumbs: [],
				bestsetting: '0',
				category: [],
				goodsCategory: false,
				datePicker: {
					type: '',
					maxDate: new Date(2050, 12),
					currentDate: new Date(),
					status: false,
				},
				records: {
					name: '',
					price: '',
					oldprice: '',
					total: '',
					cid: '',
					category_title: '',
					click: '',
					thumb: [],
					thumbs: [],
					use_limit_day: '',
					starttime_cn: '',
					endtime_cn: '',
					content: '',
					description: '',
					buy_note: '',
					falsejoinnum: '',
					falselooknum: '',
					falsesharenum: '',
					status: ''
				},
				showPreLoading: true
			}
		},
		components: {
			publicHeader,
			iswitch,
			Uploader
		},
		methods: {
			onLoad(){
				var that = this;
				getData({
					vue: that,
					url: 'manage/seckill/goods/post',
					data: {
						id: that.id
					},
					success: (res) => {
						that.records = that.util.extend(that.records, res.records);
						if(res.category){
							that.category = [...that.category, ...res.category];
						}
						if(that.records.thumbs && that.records.thumbs.length > 0) {
							for(var x in that.records.thumbs) {
								that.thumbs.push({
									url: that.records.thumbs[x]
								})
							}
						}
					}
				})
			},
			onUploadImage(e){
				var that = this;
				that.util.image({
					obj: e,
					success: function(obj, result) {
						if(result.url && result.filename) {
							that.thumb = {
								url: result.url,
								filename: result.filename,
							};
							that.thumb = Object.assign({}, that.thumb);
							that.records.thumb = that.thumb.url;
						}
					},
					options: {channel: 'h5'}
				});
			},
			onChangeThumbs(data) {
				let thumbs = [];
				if(data && data.length > 0) {
					for(var x in data) {
						thumbs.push(data[x]['url'])
					}
				}
				this.records.thumbs = thumbs;
			},
			onTogglePopup(){
				this.goodsCategory = !this.goodsCategory;
			},
			onConfirmCategory(data){
				this.records.cid = data.id;
				this.records.category_title = data.title;
				this.onTogglePopup();
			},
			onToggleDatePicker(type){
				this.datePicker.status = !this.datePicker.status;
				if(type){
					this.datePicker.type = type;
				}
			},
			onConfirmTime(value){
				var time = this.util.formatDate(value);
				if(this.datePicker.type == 'start'){
					this.records.starttime_cn = time;
				} else {
					this.records.endtime_cn = time;
				}
				this.onToggleDatePicker();
			},
			jsToggleSwitch(data){
				this.util.jsToggleSwitch({
					vue: this,
					key: data.keys,
					value: data.value
				});
			},
			onSubmit(){
				var that = this;
				if (!that.records.name) {
					that.util.$toast('名称不能为空', '', 1000);
					return false;
				}
				if (!that.records.oldprice) {
					that.util.$toast('商品原价不能为空', '', 1000);
					return false;
				}
				if (!that.records.price) {
					that.util.$toast('商品现价不能为空', '', 1000);
					return false;
				}
				setData({
					vue: that,
					url: 'manage/seckill/goods/post',
					data: {
						id: that.id,
						data: that.records
					},
					redirect: that.util.getUrl({path: '/pages/gohome/seckill/goods/list'}),
				});
			}
		},
		created() {
			if(this.$route.query && this.$route.query.id > 0) {
				this.id = parseInt(this.$route.query.id)
			}
		},
		mounted(){
			this.onLoad();
		}
	}
</script>

<style>
	#seckill-post .goods-thumb{
		width: 60px;
		height: 60px;
		border-radius: 3px;
	}
	#seckill-post .goods-thumb-wrap{
		position: relative;
	}
	#seckill-post .weui-uploader__input{
		display: block;
		width: 60px;
		height: 60px;
		opacity: 0;
		position: absolute;
		top: 0px;
		left: 3px;
	}
	#seckill-post .content .upload-img .uploader{
		padding: 0;
	}
	#seckill-post .content .upload-img .goods-img{
		padding-bottom: 10px;
		color: #333;
		font-size: 14px;
	}
</style>


